import React from 'react';
import BtnDownloadApp from '../common/buttons/BtnDownloadApp';
import styled from 'styled-components';
import { media, rem } from '../../lib/styles/variables';

const CarsBannerBlock = styled.div`
  display: none;
  background-color: #ebf7fa;
  border-radius: 10px;
  padding: ${rem(24)};
  ${media('medium')} {
    display: flex;
    align-items: center;
    gap: ${rem(15)};
    p {
      flex: 1;
      line-height: 1.5;
      font-weight: 700;
    }
  }

  ${media('small')} {
    flex-direction: column;
    align-items: flex-start;
    padding: ${rem(16)};
    svg {
      display: none;
    }
    p {
      font-size: ${rem(14)};
    }
  }
`;

const CarsBanner = () => {
  return (
    <CarsBannerBlock>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        xmlnsXlink="http://www.w3.org/1999/xlink"
        width="60"
        height="49"
        fill="none"
        viewBox="0 0 60 49"
        aria-label="중고차 로고"
        class=""
      >
        <path fill="url(#icon_banner_car_svg__a)" d="M0 0h60v49H0z"></path>
        <defs>
          <pattern
            id="icon_banner_car_svg__a"
            width="1"
            height="1"
            patternContentUnits="objectBoundingBox"
          >
            <use
              href="#icon_banner_car_svg__b"
              transform="matrix(.00716 0 0 .00877 -.001 0)"
            ></use>
          </pattern>
          <image
            href="data:image/png;base64,"
            id="icon_banner_car_svg__b"
            width="140"
            height="114"
          ></image>
        </defs>
      </svg>
      <p>
        딜러 없이 믿고 살 수 있는 중고차,
        <br />
        당근에서 직거래해보세요.
      </p>
      <BtnDownloadApp type="darkButton" />
    </CarsBannerBlock>
  );
};

export default CarsBanner;
